





















import { Component, Prop, Vue, Watch } from "vue-property-decorator";
@Component({ components: {}, name: "Rocode" })
export default class Rocode extends Vue {
  animation2: any = null;
  animation1: any = null;
  animation3: any = null;
  spreakingAnimation1: any = null;
  spreakingAnimation2: any = null;
  spreakingAnimation3: any = null;
  @Prop({
    default: false
  })
  private isRecord!: boolean;
  onReady() {
    this.animation1 = uni.createAnimation({
      duration: 1000,
      timingFunction: "ease-out"
    });
    this.animation2 = uni.createAnimation({
      duration: 1000,
      timingFunction: "ease-out"
    });
    this.animation3 = uni.createAnimation({
      duration: 1000,
      timingFunction: "ease-out"
    });
  }
  @Watch("isRecord")
  watchRecord() {
    if (this.isRecord) {
      this.speaking();
    }
  }

  async touchstart() {
    if (await this.checkPrimisse()) {
      this.$emit('startRecord')
    }
  }
  //检查权限
  checkPrimisse() {
    return new Promise(resolve => {
      uni.getSetting({
        success(res) {
          if (!res.authSetting["scope.record"]) {
            //调取小程序新版授权页面
            uni.authorize({
              scope: "scope.record",
              success() {
                console.log("录音授权成功");
                resolve(true);
                // 用户已经同意小程序使用录音功能
              },
              fail() {
                console.log("第一次录音授权失败");
                uni.showModal({
                  title: "提示",
                  content: "您未授权录音，功能将无法使用",
                  showCancel: true,
                  confirmText: "授权",
                  confirmColor: "#52a2d8",
                  success: function(res: any) {
                    if (res.confirm) {
                      //确认则打开设置页面（重点）

                      uni.openSetting({
                        success: uni => {
                          console.log(res.authSetting);
                          if (!res.authSetting["scope.record"]) {
                            console.log("未设置录音授权");
                            uni.showModal({
                              title: "提示",
                              content: "您未授权录音，功能将无法使用",
                              showCancel: false
                            });
                          } else {
                            //第二次才成功授权
                            console.log("设置录音授权成功");
                            resolve(true);
                          }
                        },

                        fail: function() {
                          resolve(false);
                          console.log("授权设置录音失败");
                        }
                      });
                    } else if (res.cancel) {
                      resolve(false);
                      console.log("cancel");
                    }
                  },

                  fail: function() {
                    console.log("openfail");
                    resolve(false);
                  }
                });
              }
            });
          } else {
            resolve(true);
          }
        }
      });
    });
  }
  speaking() {
    //波纹放大,淡出动画
    var _this = this;
    setTimeout(() => {
      this.animationFun(1, 1000);
    }, 0);
    setTimeout(() => {
      this.animationFun(2, 1000);
    }, 300);
    setTimeout(() => {
      this.animationFun(3, 1000);
    }, 600);
  }
  animationFun(index: number, time: any) {
    //波纹放大,淡出动画
    if (this.isRecord) {
      setTimeout(() => {
        this.animationFun(index, time);
      }, time);
    }
    switch (index) {
      case 1:
        this.animation1
          .scale(1, 1)
          .opacity(1)
          .step({ duration: 0 }); //还原
        this.spreakingAnimation1 = this.animation1.export();
        break;
      case 2:
        this.animation2
          .scale(1, 1)
          .opacity(1)
          .step({ duration: 0 }); //还原
        this.spreakingAnimation2 = this.animation2.export();
        break;
      case 3:
        this.animation3
          .scale(1, 1)
          .opacity(1)
          .step({ duration: 0 }); //还原
        this.spreakingAnimation3 = this.animation3.export();
        break;
    }
    setTimeout(() => {
      switch (index) {
        case 1:
          this.animation1
            .scale(2, 2)
            .opacity(0)
            .step(); //修改透明度,放大
          this.spreakingAnimation1 = this.animation1.export();
          break;
        case 2:
          this.animation2
            .scale(2, 2)
            .opacity(0)
            .step(); //修改透明度,放大
          this.spreakingAnimation2 = this.animation2.export();
          break;
        case 3:
          this.animation3
            .scale(2, 2)
            .opacity(0)
            .step(); //修改透明度,放大
          this.spreakingAnimation3 = this.animation3.export();
          break;
      }
    }, 100);
  }
}
